.project-link {
  background-color: #fff;
  background-size: 30px;
  background-position-x: 5px;
  background-position-y: center;
  background-repeat: no-repeat;
  display: block;
  height: 40px;
  line-height: 40px;
  padding: 0 10px 0 40px;
  border-radius: 5px;
  -webkit-box-shadow: 2px 2px 5px rgb(40, 40, 40);
          box-shadow: 2px 2px 5px rgb(40, 40, 40);
  text-decoration: none;
  font-family: sans-serif;
  margin: 0 20px;
  text-transform: capitalize;
  -webkit-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
}

.project-link:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-box-shadow: 4px 4px 10px rgb(40, 40, 40);
          box-shadow: 4px 4px 10px rgb(40, 40, 40);
}

.github {
  background-image: url("/assets/github-logo.png");
}

.production {
  background-image: url("/assets/production-logo.png");
}



