.projects {
  width: 100%;
  height: calc(100vh - 80px);
  padding: 10px 30px;
  margin: 0 auto;
  background: transparent;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  -ms-flex-pack: center;
      justify-content: center;
}



